.main {
    max-width: 1100px;
    padding: 1rem;
}

.table {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 0.5rem;
    margin-block: 3rem;
    
    .row {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        font-size: 0.8rem;
        border-radius: 0.4rem;
        overflow: hidden;
    }
    
    .row > div {
        margin: 0;
        padding: 0.2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .name {
        color: darkgoldenrod;
    }
    .hex {
    }
    .rgb {
    }
    .box {
    }
}

@media (max-width: 980px) {
    .table {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 768px) {
    .main {
        font-size: 0.8rem;
    }
}

@media (max-width: 550px) {
    .table {
        grid-template-columns: repeat(1, 1fr);
    }
}
